.pagination {
  margin-top: 20px;
  margin-bottom: unset;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a {
  background-color: #04724d;

  border-color: #121732;
  color: #ffffff;
}

.pagination > li {
  margin: 10px 15px 0px 0px;
}
.pagination > li > a {
  padding: 5px 12px;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  margin-top: 5px;
}

.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  outline: none;
  color: #ffffff !important;
}
.pagination > li > a,
.pagination > li > span {
  color: #0e0e0e;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: unset;
}

.pagination > li:last-child {
  background-color: none;
  border-radius: 0;
  padding: 0px;
  margin-right: 0px;
}

.pagination-bottom {
  display: flex;
  justify-content: center;
}

.ReactTable {
  border: none;
}

.ReactTable .rt-thead .rt-tr {
  color: #ffffff;
  border-radius: 4px 4px 0 0;
  /* background: #04724d; */
  font-size: 18px;
  font-weight: bolder;
}

.ReactTable .rt-thead.-header {
  box-shadow: none;
}

.ReactTable .rt-thead .rt-th,
.ReactTable .rt-thead .rt-td {
  border-right: none;
}

.ReactTable .rt-tbody .rt-td {
  border-right: 1px solid #36363659;
}

.ReactTable .rt-tbody .rt-tr-group {
  border: 1px solid #36363659;
  border-bottom: none;
}

.ReactTable .rt-th,
.ReactTable .rt-td {
  text-align: center;
}

.ReactTable .rt-tbody .rt-tr-group:last-child {
  border: 1px solid #36363659;
  border-radius: 0 0 4px 4px;
}

.Next-button {
  background-color: #f4f9f5;
}
.Next-button .MuiButton-contained {
  background-color: #04724d;
  color: #ffffff;
  padding: 0px;
  border-radius: 35px;
  font-size: 20px;
  text-transform: none;
  padding: 8px 5px;
}

.Next-button .MuiButton-contained:hover {
  background-color: #04724d;
}

.arrow-icon {
  margin-left: 15px;
}
.ReactTable .rt-thead .rt-th {
  outline: none;
}
.ReactTable .rt-resizer {
  z-index: 0 !important;
}
