.show-cand-cards {
  display: flex;
  width: auto;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px !important;
  margin-right: 20px;
  border: 1px solid #ccc !important;
  border-radius: 6px !important;
}
.show-tick {
  background: transparent url("../../assets/images/status-right.svg") 0% 0%
    no-repeat padding-box;
  width: 51px;
  height: 48px;
}
.show-rejected {
  background: transparent url("../../assets/images/status-rejected.svg") 0% 0%
    no-repeat padding-box;
  width: 51px;
  height: 48px;
}
.show-total {
  background: transparent url("../../assets/images/status-total.svg") 0% 0%
    no-repeat padding-box;
  width: 51px;
  height: 48px;
}
.show-cand-status {
  display: flex;
}
.show-cand-card {
  margin: 30px;
  background-color: #fff;
  padding: 30px 50px;
}
.show-text h3 {
  display: flex;
  justify-content: flex-end;
  font: normal normal 600 28px/47px Poppins;
}
.show-text p {
  text-align: right;
  margin-bottom: unset;
  font: normal normal 600 12px/18px Poppins;
}
.rs-search-bar {
  margin-bottom: 10px;
  justify-content: center;
  height: 60px;
  width: 99%;
}
.rs-search-div {
  display: flex;
  width: 100%;
  margin-bottom: 0px;
  border: none;
}

.rs-search-div .flex {
  margin-top: 20px;
  width: 100%;
}
.MuiInputBase-root {
  width: 100%;
  background: #e1e5eb 0% 0% no-repeat padding-box;
}
.text1 {
  color: #04724d;
}
.text2 {
  color: #dd2c2c;
}
.text3 {
  color: #c69e5d;
}
.MuiInputBase-root {
  padding-left: unset;
}
.rt-tr .rt-td {
  text-align: center;
}
.candidate-button {
  display: flex;
  justify-content: flex-end;
}
.MuiIconButton-colorPrimary {
  color: #04724d !important;
}
